export async function solveDay5(input) {
    let partA = 0;
    let partB = 0;

    var ranges = input.split("\n\n")[0].split("\n").map(line => line.trim());
    var avail = input.split("\n\n")[1].split("\n").map(line => line.trim());

    let newRange = [];

    ranges = ranges.forEach(range => {
        range = range.split("-");
        
        for (let i = range[0]; i <= range[1]; i++) {
            //newRange.push(parseInt(i));
        }
    })

    avail.forEach(num => {
        if (newRange.includes(parseInt(num))) {
            partA++;
        }
    })


    console.log(partA);
    return { partA, partB };
}